@import './structure/fonts';
@import './structure/variables';

@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;0,700;1,400;1,700&display=swap');

body#tinymce {

	p {
		font-family: 'Alegreya Sans';
	}

	.cite-highlight {
		display: block;
		margin-top: 8px;
		color: #8B99A7;
		font-style: normal;
		border-top: 1px solid #E0E6EB;;
	}

	blockquote {
	
		p {
			font-family: 'Alegreya Sans';
			font-style: normal;
			font-weight: normal;
			font-size: 16px;
			line-height: 19px;
			color: #8b99a7;
		}

		strong {
			display: block;
			font-family: 'campunibold';
			font-weight: normal;
			font-size: 40px;
			line-height: 48px;
		}
	
	}

}
